<template>
    <v-container>
      <v-row class="ma-0 pa-0">
        <v-col align="left" class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
          <vue-country-code style="display:none" />
        </v-col>
        <v-col align="left" class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
          <v-menu v-model="fromDateMenu" :close-on-content-click="false" :nudge-right="40" transition="scale-transition" offset-y min-width="auto">
            <template v-slot:activator="{ on, attrs }">
            <v-text-field v-model="fromDate" label="From" prepend-icon="mdi-calendar" readonly v-bind="attrs" v-on="on" hide-details style="width: 200px"></v-text-field>
            </template>
            <v-date-picker v-model="fromDate" @input="fromDateMenu = false"></v-date-picker>
          </v-menu>        
        </v-col>
        <v-col align="left" class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
          <v-menu v-model="toDateMenu" :close-on-content-click="false" :nudge-right="40" transition="scale-transition" offset-y min-width="auto">
            <template v-slot:activator="{ on, attrs }">
            <v-text-field v-model="toDate" label="To" prepend-icon="mdi-calendar" readonly v-bind="attrs" v-on="on" hide-details style="width: 200px"></v-text-field>
            </template>
            <v-date-picker v-model="toDate" @input="toDateMenu = false"></v-date-picker>
          </v-menu>
        </v-col>            
        <v-col class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
          <v-text-field hide-details label="Search" placeholder="Search" dense outlined v-model="searchText" @keyup="keyEnter" ref="search" style="width: 270px;top:10px"></v-text-field>
        </v-col>
        <v-col align="left" class="ma-0 pa-0 flex-grow-0 flex-shrink-0">
          <v-tooltip top color="#9BC298">
            <template v-slot:activator="{ on, attrs }">
              <v-img v-on="on" v-bind="attrs" style="cursor:pointer;top:10px" alt="" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/magnify.png" @click="setSearch" width="40px" />
            </template>
            <span>Search</span>
          </v-tooltip>
        </v-col> 
        <v-col class="ma-1 pa-1 flex-grow-0 flex-shrink-0"> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; </v-col>
        <v-col class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
          <v-tooltip top color="#9BC298">
            <template v-slot:activator="{ on, attrs }">
              <v-img v-on="on" v-bind="attrs" style="cursor:pointer;top:5px" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/shpping.png" width="40px" @click="carrierSelect()" />
            </template>
            <span>Shipping Method</span>
          </v-tooltip>
        </v-col>
        <v-col class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
          <v-tooltip top color="#9BC298">
            <template v-slot:activator="{ on, attrs }">
              <v-img v-on="on" v-bind="attrs" style="cursor:pointer;top:5px" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/barcode_toolbar.png" width="45px"  @click="getBarcodeView()" />
            </template>
            <span>Print GEM Barcode</span>
          </v-tooltip>
        </v-col>
        <v-col class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
          <v-tooltip top color="#9BC298">
            <template v-slot:activator="{ on, attrs }">
              <v-img v-on="on" v-bind="attrs" style="cursor:pointer;top:5px" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/awb_toolbar.png" width="40px"  @click="getAwbView('')" />
            </template>
            <span>Print AWB</span>
          </v-tooltip>
        </v-col>
        <v-col class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
          <v-tooltip top color="#9BC298">
            <template v-slot:activator="{ on, attrs }">
              <v-img v-on="on" v-bind="attrs" style="cursor:pointer;top:5px" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/download-file_toolbar.png" width="40px" @click="getMakeExcel()"/>
            </template>
            <span>Download Excel</span>
          </v-tooltip>
        </v-col>
        <v-col class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
          <v-tooltip top color="#9BC298">
            <template v-slot:activator="{ on, attrs }">
              <v-img v-on="on" v-bind="attrs" style="cursor:pointer;top:5px" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/reload.png" width="40px" @click="getDataInit()" />
            </template>
            <span>Refresh</span>
          </v-tooltip>
        </v-col>
        <v-col class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
          <v-tooltip top color="#9BC298">
            <template v-slot:activator="{ on, attrs }">
              <v-img v-on="on" v-bind="attrs" style="cursor:pointer;top:5px" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/rubbish-bin_toolbar.png" width="40px" @click="setMultiDelete()" />
            </template>
            <span>Delete</span>
          </v-tooltip>
        </v-col>
        <v-col align="right" class="ma-1 pa-0 flex-shrink-0">
        </v-col>
        <v-col align="right" class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
          <v-tooltip top color="#D18EA8">
            <template v-slot:activator="{ on, attrs }">
              <v-img v-on="on" v-bind="attrs" style="cursor:pointer" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/gts.png" width="40px" @click="getManifestMakeExcel()"/> 
            </template>
            <span>Download Manifest</span>
          </v-tooltip>
        </v-col>
        <v-col align="right" class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
          <v-tooltip top color="#D18EA8">
            <template v-slot:activator="{ on, attrs }">
              <v-img v-on="on" v-bind="attrs" style="cursor:pointer" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/export_declaration_icon.png" width="40px" @click="exportDeclarationList()"/> 
            </template>
            <span>Download Export-Declaration</span>
          </v-tooltip>
        </v-col>
      </v-row>
      <v-row>
        <v-col align="left" style="padding-top:0px;padding-bottom:0px;">
          Total : {{ desserts.length }} / Selected : {{ selected.length }}
        </v-col>
      </v-row>    
      <v-row>
        <v-col>
        <v-data-table :headers="headers" :items="desserts" item-key="id" class="elevation-1" disable-pagination hide-default-footer multi-sort fixed-header height="100%" id="dataTableContent">
        <template v-slot:item="props">
          <tr>
            <td style="height:30px;" align="center">
              <input type="checkbox" style="width:20px;height:20px" @change="setCheck(props.item, $event.target.checked)" name="item">
            </td>
            <td align="start" style="height:30px; ma-0 pa-0; cursor:pointer" @click="getOrderDetail(props.item.order)">
              {{ props.item.order.gemMasterNo }}
            </td>
            <td style="height:30px; ma-0 pa-0" align="start">
              <v-row>
                <v-col align="left" class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
                  <img alt="" class="shrink mt-1 hidden-sm-and-down" contain :src="getPlatformImgUrl(props.item.order.storePlatform)" width="20px"/>
                </v-col>
                <v-col align="left" class="ma-1 pa-1  flex-shrink-0">
                  <div style="width:90px;overflow:hidden;white-space:nowrap;text-overflow:ellipsis">{{ props.item.order.orderNo }}</div>
                </v-col>
              </v-row>
            </td>
            <td style="height:30px; ma-0 pa-0" align="start">{{ props.item.order.orderedAt }}</td>
            <td style="height:30px; ma-0 pa-0" align="start">
              <div style="width:90px;overflow:hidden;white-space:nowrap;text-overflow:clip">{{ props.item.order.recipientName }}</div><div style="margin:1px" :class="getCountry(props.item.order.recipientAddressCountryCode)"></div>&nbsp;({{props.item.order.recipientAddressCountryCode}})
            </td>
            <td v-if="props.item.order.orderItems.length != 0" style="height:30px; ma-0 pa-0" align="start">
              <div style="width:150px;overflow:hidden;white-space:nowrap;text-overflow:clip">{{ props.item.order.orderItems[0].itemQuantity }} X {{ props.item.order.orderItems[0].itemName }}<br />
              Qty: {{ props.item.order.orderItems | totalQuantity }}, Value: {{ props.item.order.orderItems[0].itemValueCurrency | currency }} {{ props.item.order.itemTotalValue | comma }}  
              </div>
            </td>
            <td v-else style="height:30px; ma-0 pa-0" align="start">
                Qty: {{ props.item.order.orderItems | totalQuantity }}, Value: {{ props.item.order.itemTotalValue | comma }}  
            </td>
            <td style="height:30px; ma-0 pa-0" align="start">
              {{ props.item.order.shipmentActualWeight }} <span v-if="props.item.order.weightUnit == null || props.item.order.weightUnit == ''">kg</span><span v-else>{{ props.item.order.weightUnit }}</span> 
               / {{ props.item.order.shipmentVolumeWeight }} <span v-if="props.item.order.weightUnit == null || props.item.order.weightUnit == ''">kg</span><span v-else>{{ props.item.order.weightUnit }}</span> 
            </td>
            <td v-if="props.item.totalValue == 0" style="height:30px; ma-0 pa-0" align="start">
            </td>
            <td v-else>
              <img alt="" class="shrink mt-1 hidden-sm-and-down" contain :src="getRateImgUrl(props.item.carrier)" width="20px" /><br/> {{ props.item.currency | currency }} {{ props.item.totalValue | comma }}
            </td>
            <td style="height:30px; ma-0 pa-0" align="start">{{ props.item.order.shippingTerm }}</td>
            <td style="height:30px; ma-0 pa-0" align="start">{{ props.item.order.trackingEventAbbreviation }}</td>
            <td v-if="props.item.order.transitDay == null || props.item.order.transitDay == ''" style="height:30px; ma-0 pa-0" align="start"></td>
            <td v-else>{{ props.item.order.transitDay }} Days</td>
            <td v-if="props.item.order.serviceType == null || props.item.order.serviceType == '' || props.item.order.serviceType == 'NULL'" style="height:30px; ma-0 pa-0" align="start"></td>
            <td v-else>{{ props.item.order.serviceType }}</td>
            <td style="height:30px; ma-0 pa-0" align="left">
              <v-row>
                <v-col class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
                  <div class="tooltip">
                    <img v-if="props.item.order.paymentStatus === 'WAIT'" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/sms_purple.png" width="25px" @click="setSmsResend(props.item)" style="cursor:pointer" />
                    <img v-else-if="props.item.order.paymentStatus === 'COMPLETE'" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/sms_purple.png" width="25px" />
                    <img v-else class="shrink mt-1 hidden-sm-and-down" contain src="@/images/sms_black.png" width="25px" />
                    <span class="tooltiptext">SMS Send</span>
                  </div>
                </v-col>
                <v-col class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
                  <div class="tooltip">
                    <img v-if="props.item.order.paymentStatus === 'COMPLETE'" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/paymented_purple.png" width="22px" />
                    <img v-else class="shrink mt-1 hidden-sm-and-down" contain src="@/images/paymented_black.png" width="22px" />
                    <span class="tooltiptext">Payment</span>
                  </div>
                </v-col>
                <v-col class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
                  <div class="tooltip">
                    <img v-if="props.item.isPrintBarcode === 'Y'" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/barcode_purple.png" width="25px" @click="getBarcodeViewRow(props.item)" style="cursor:pointer" />
                    <img v-else class="shrink mt-1 hidden-sm-and-down" contain src="@/images/barcode_black.png" width="25px" @click="getBarcodeViewRow(props.item)" style="cursor:pointer" />            
                    <span class="tooltiptext">Barcode Print</span>
                  </div>
                </v-col>
                <v-col class="ma-1 pa-0 flex-grow-0 flex-shrink-0" style="display:block">
                  <div class="tooltip">
                    <img v-if="props.item.isPrintAWB === 'Y'" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/awb_purple.png"  width="25px" @click="getAwbView(props.item)" style="cursor:pointer" />
                    <img v-else class="shrink mt-1 hidden-sm-and-down" contain src="@/images/awb_black.png"  width="25px" @click="getAwbView(props.item)" style="cursor:pointer" />
                    <span class="tooltiptext">AWB Print</span>
                  </div>
                </v-col>
                <v-col class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
                  <div class="tooltip">
                    <img v-if="props.item.isPrintBarcode === 'Y' || props.item.isPrintAWB === 'Y'" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/traking_purple.png"  width="25px" @click="trackingView(props.item)" style="cursor:pointer" />
                    <img v-else class="shrink mt-1 hidden-sm-and-down" contain src="@/images/traking_black.png"  width="25px" @click="trackingView(props.item)" style="cursor:pointer" />
                    <span class="tooltiptext">Tracking View</span>
                  </div>
                </v-col>
                <v-col class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
                  <div class="tooltip">
                    <img class="shrink mt-1 hidden-sm-and-down" contain src="@/images/ordermodify_black.png"  width="25px" @click="orderDetailModify(props.item)" style="cursor:pointer" />
                    <span class="tooltiptext">Order Modify</span>
                  </div>
                </v-col>
                <v-col class="ma-1 pa-0 flex-grow-0 flex-shrink-0">
                  <div class="tooltip">
                    <img class="shrink mt-1 hidden-sm-and-down" contain src="@/images/rubbish-bin1.png" width="25px" @click="setDelete(props.item)" style="cursor:pointer" />
                    <span class="tooltiptext">Shipment Delete</span>
                  </div>
                </v-col>
              </v-row>
            </td>
          </tr>
        </template>
        </v-data-table>
        </v-col>
      </v-row>  
  
      <!--    -->
      <v-dialog v-model="orderDetailModifyDialog" persistent max-width="1300px">
        <template v-slot:activator="{ on, attrs }">
          <v-btn color="primary" dark v-bind="attrs" v-on="on" disabled style="display: none"> Open Dialog </v-btn>
        </template>
        <v-card id="dialog">
          <v-card-text class="ma-0 pa-0">
            <v-container>
              <v-row class="ma-0 pa-0">
                <v-col align="left" class="ma-0 pa-0">
                  <v-row class="ma-0 pa-0">
                    <v-col class="ma-0 pa-0">
                      <table style="margin-bottom: 10px">
                        <tr>
                          <td>
                            <v-img
                              alt=""
                              class="shrink mt-1 hidden-sm-and-down"
                              contain
                              src="@/images/info.png"
                              width="40px"
                            />
                          </td>
                          <td>
                            <b>{{ orderDetail.gemMasterNo }}</b>
                          </td>
                        </tr>
                      </table>
                    </v-col>
                  </v-row>
                  <v-row class="ma-0 pa-0">
                    <v-col class="ma-1 pa-0" style="display: flex">
                      <v-text-field
                        hide-details
                        label="Marketplace/Platform (*)"
                        placeholder="Marketplace/Platform (*)"
                        outlined
                        v-model="orderDetail.storePlatform"
                        ref="storePlatform"
                        dense
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-row class="ma-0 pa-0">
                    <v-col class="ma-1 pa-0" style="display: flex">
                      <v-text-field
                        hide-details
                        label="Store Name (*)"
                        placeholder="Store Name (*)"
                        outlined
                        v-model="orderDetail.storeName"
                        ref="storeName"
                        dense
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-row class="ma-0 pa-0">
                    <v-col class="ma-1 pa-0" style="display: flex">
                      <v-text-field
                        hide-details
                        label="Order Number (*)"
                        placeholder="Order Number (*)"
                        outlined
                        v-model="orderDetail.orderNo"
                        ref="orderNo"
                        dense
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-row class="ma-0 pa-0">
                    <v-col class="ma-1 pa-0" style="display: flex">
                      <v-menu v-model="orderDateMenu" :close-on-content-click="false" :nudge-right="40" transition="scale-transition" offset-y min-width="auto">
                        <template v-slot:activator="{ on, attrs }">
                          <v-text-field v-model="orderDetail.orderedAt" label="Order Date (*)" dense outlined readonly v-bind="attrs" v-on="on" hide-details style="width: 200px" :disabled="actionMethod == 'Modify'"></v-text-field>
                        </template>
                        <v-date-picker v-model="orderDetail.orderedAt" @input="orderDateMenu=false" :disabled="actionMethod == 'Modify'"></v-date-picker>
                      </v-menu>       
                    </v-col>
                  </v-row>
                  <v-row class="ma-0 pa-0">
                    <v-col class="ma-1 pa-0" style="display: flex">
                      <v-select
                        hide-details
                        :items="shippingTermItems"
                        label="Shipping Term (*)"
                        outlined
                        dense
                        style="width: 100px"
                        v-model="orderDetail.shippingTerm"
                        ref="shippingTerm"
                        placeholder="Shipping Term (*)"
                      ></v-select>
                    </v-col>
                  </v-row>
                  <v-row class="ma-0 pa-0">
                    <v-col class="ma-1 pa-0" style="display: flex">
                      <v-select
                        hide-details
                        :items="packageTypeItems"
                        item-text="name"
                        item-value="value"
                        style="width:100px"
                        label="Package Type"
                        placeholder="Package Type"
                        v-model="orderDetail.packageType"
                        ref="packageType"
                        outlined
                        dense
                      ></v-select>
                    </v-col>
                  </v-row>
                  <v-row class="ma-0 pa-0">
                    <v-col class="ma-1 pa-0" style="display: flex">
                      <v-select
                        hide-details
                        :items="contentsTypeItems"
                        item-text="name"
                        item-value="value"                          
                        style="width:100px"
                        label="Contents Type (*)"
                        placeholder="Contents Type (*)"
                        v-model="orderDetail.itemContentsType"
                        ref="itemContentsType"
                        dense
                        outlined
                      ></v-select>
                    </v-col>
                  </v-row>
                  <v-row class="ma-0 pa-0">
                    <v-col class="ma-1 pa-0" style="display: flex">
                      <v-select
                        hide-details
                        :items="serviceTypeItems"
                        item-text="name"
                        item-value="value"
                        label="Service Type"
                        outlined
                        dense
                        style="width: 100px"
                        v-model="orderDetail.serviceType"
                        ref="serviceType"
                        placeholder="Service Type"
                      ></v-select>
                    </v-col>
                  </v-row>
                </v-col>
  
                <v-col align="left" class="ma-0 pa-0">
                  <v-row class="ma-0 pa-0">
                    <v-col class="ma-0 pa-0">
                      <table style="margin-bottom: 10px">
                        <tr>
                          <td>
                            <v-img
                              alt=""
                              class="shrink mt-1 hidden-sm-and-down"
                              contain
                              src="@/images/sender.png"
                              width="40px"
                            />
                          </td>
                          <td><b>Sender</b></td>
                        </tr>
                      </table>
                    </v-col>
                  </v-row>
                  <v-row class="ma-0 pa-0">
                    <v-col class="ma-1 pa-0" style="display: flex">
                      <v-text-field
                        hide-details
                        label="Name (*)"
                        placeholder="Name (*)"
                        outlined
                        v-model="orderDetail.senderName"
                        ref="senderName"
                        dense
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-row class="ma-0 pa-0">
                    <v-col class="ma-1 pa-0" style="display: flex">
                      <v-text-field
                        hide-details
                        label="Company"
                        placeholder="Company"
                        outlined
                        v-model="orderDetail.senderCompany"
                        ref="senderCompany"
                        dense
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-row class="ma-0 pa-0">
                    <v-col class="ma-1 pa-0" style="display: flex">
                      <v-text-field
                        hide-details
                        label="Email (*)"
                        placeholder="Email (*)"
                        outlined
                        v-model="orderDetail.senderEmail"
                        ref="senderEmail"
                        dense
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-row class="ma-0 pa-0">
                    <v-col class="ma-1 pa-0" style="display: flex">
                      <v-text-field
                        hide-details
                        label="Phone Number (*)"
                        placeholder="Phone Number (*)"
                        outlined
                        v-model="orderDetail.senderPhoneNo"
                        oninput="javascript: this.value = this.value.replace(/\D/g, '' );"
                        ref="senderPhoneNo"
                        dense
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-row class="ma-0 pa-0">
                    <v-col class="ma-1 pa-0" style="display: flex">
                      <v-text-field
                        hide-details
                        label="Mobile Number (*)"
                        placeholder="Mobile Number (*)"
                        outlined
                        v-model="orderDetail.senderMobileNo"
                        oninput="javascript: this.value = this.value.replace(/\D/g, '' );"
                        ref="senderMobileNo"
                        dense
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-row class="ma-0 pa-0">
                    <v-col class="ma-1 pa-0" style="display: flex">
                      <vue-country-code
                        @onSelect="onCountrySelectSender"
                        v-bind:defaultCountry="orderDetail.senderAddressCountryCode"
                        style="margin-right: 5px"
                      >
                      </vue-country-code>
                      <v-text-field
                        hide-details
                        label="Country Code"
                        placeholder="Country Code"
                        outlined
                        v-model="orderDetail.senderAddressCountryCode"
                        ref="senderAddressCountryCode"
                        dense
                        filled
                        disabled
                      ></v-text-field>
                    </v-col>
                  </v-row>              
                  <v-row class="ma-0 pa-0">
                    <v-col class="ma-1 pa-0" style="display: flex">
                      <v-text-field
                        hide-details
                        label="Street1 (*)"
                        placeholder="Street1 (*)"
                        outlined
                        v-model="orderDetail.senderAddress1"
                        ref="senderAddress1"
                        dense
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-row class="ma-0 pa-0">
                    <v-col class="ma-1 pa-0" style="display: flex">
                      <v-text-field
                        hide-details
                        label="Street2"
                        placeholder="Street2"
                        outlined
                        v-model="orderDetail.senderAddress2"
                        ref="senderAddress2"
                        dense
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-row class="ma-0 pa-0">
                    <v-col class="ma-1 pa-0" style="display: flex">
                      <v-text-field
                        hide-details
                        label="City (*)"
                        placeholder="City (*)"
                        outlined
                        v-model="orderDetail.senderAddressCity"
                        ref="senderAddressCity"
                        dense
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-row class="ma-0 pa-0" id="senderAddrssStateSelect" style="display:none">
                    <v-col class="ma-1 pa-0" style="display: flex">
                      <v-select
                        hide-details
                        :items="senderAddressStateItems"
                        item-text="name"
                        item-value="value"
