      <v-dialog v-model="carrierDialog" persistent max-width="850px">
        <template v-slot:activator="{ on, attrs }">
          <v-btn color="primary" dark v-bind="attrs" v-on="on" disabled style="display: none"> Open Dialog </v-btn>
        </template>
        <v-card>
          <v-card-text class="ma-0 pa-0">
            <v-container>
              <v-row>
                <v-col align="center" class="ma-0 pa-0">
                  <v-img alt="" contain src="@/images/carriertop2.png" style="margin-top: 10px;width:820px"/>
                </v-col>
              </v-row>
              <v-row>
                <v-col align="center">
                  <table style="border:0px;width:820px">
                    <template  v-for="(item, index) in this.shippingMethodList">
                      <tr style="height:50px" :key="index">                
                          <td style="width:100px"><v-img style="" alt="" class="shrink mt-1 hidden-sm-and-down" contain :src="getCarrierServiceImage(item.carrierService)" width="50px" /></td>
                          <td style="width:200px"><b>{{ item.carrierService }}</b></td>
                          <td v-if="item.totalCount == 1" style="width:200px">{{ item.totalCount }} order</td>
                          <td v-else style="width:200px">{{ item.totalCount }} orders</td>
                          <td style="width:200px">KRW {{ item.totalValue | comma }}</td>
                          <td style="width:100px" align="right"><v-btn color="primary" @click="setShippngMethod(item)"> Choose </v-btn></td>
                      </tr>
                    </template>
                  </table>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>            
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="error" outlined @click="setCarrierCancel"> Cancel </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <!--    -->
  
      <!--   -->
      <v-dialog v-model="trackingDialog" persistent max-width="850px">
        <template v-slot:activator="{ on, attrs }">
          <v-btn color="primary" dark v-bind="attrs" v-on="on" disabled style="display: none"> Open Dialog </v-btn>
        </template>
        <v-card>
          <v-card-text class="ma-0 pa-0">
            <v-container>
              <v-row>
                <v-col align="center">
                  <table style="border:0px;width:820px">
                    <tr>
                        <td colspan="5" style="background-color:#6587cd;width:100%;height:80px">
                          <table style="font-color:red;width:100%">
                              <tr>
                                  <td style="padding-left:10px"><font style="color:#ffffff">Your Parcel Status:</font></td>
                                  <td rowspan="3" align="right"><v-img style="" alt="" class="shrink mt-1 hidden-sm-and-down" contain src="@/images/tracking_top.png" width="230px" /></td>
                              </tr>
                              <tr>
                                  <td style="height:50px;padding-left:10px"><font style="color:#ffffff"><h1>{{ shipmentDetail.trackingEventAbbreviation }}</h1></font></td>
                              </tr>
                              <tr>
                                  <td style="padding-left:10px" v-if="shipmentDetail != ''"><font style="color:#ffffff">Tracking #: {{ shipmentDetail.order.gemMasterNo }}</font></td>
                              </tr>
                          </table>
                        </td>
                    </tr>
                    <tr>
                      <td>
                        <table style="padding-top:10px;padding-bottom:10px;width:100%">
                            <tr height="50px">
                                <td align="center" id="trackingStep1" style="background-color:#9E9E9E; width:20%"><font style="color:#ffffff">Label Created</font></td>
                                <td align="center" id="trackingStep2" style="background-color:#9E9E9E; width:20%"><font style="color:#ffffff">Shipped</font></td>
                                <td align="center" id="trackingStep3" style="background-color:#9E9E9E; width:20%"><font style="color:#ffffff">Clearance in<br>Progress</font></td>
                                <td align="center" id="trackingStep4" style="background-color:#9E9E9E; width:20%"><font style="color:#ffffff">Out for Delivery</font></td>
                                <td align="center" id="trackingStep5" style="background-color:#9E9E9E; width:20%"><font style="color:#ffffff">Delivered</font></td>
                            </tr>
                        </table>
                      </td>
                    </tr>
                    <tr>
                      <td>
                        <table style="padding-left:10px;width:100%">
                          <tr>
                              <td style="width:140px"><font style="color:#be2b9b">Order #:</font></td>
                              <td v-if="shipmentDetail != ''">{{ shipmentDetail.order.orderNo }}</td>
                          </tr>
                          <tr>
                              <td><font style="color:#be2b9b">Sender:</font></td>
                              <td v-if="shipmentDetail != ''">{{ shipmentDetail.order.senderName }}</td>
                          </tr>
                          <tr>
                              <td><font style="color:#be2b9b">Shpping to:</font></td>
                              <td v-if="shipmentDetail != ''">{{ shipmentDetail.order.recipientName }}</td>
                          </tr>
                          <tr>
                              <td></td>
                              <td v-if="shipmentDetail != ''">{{ shipmentDetail.order.recipientPhoneNo }}</td>
                          </tr>
                          <tr>
                              <td></td>
                              <td v-if="shipmentDetail != ''">{{ shipmentDetail.order.recipientAddress1 }} {{ shipmentDetail.order.recipientAddress2 }}</td>
                          </tr>
                          <tr>
                              <td></td>
                              <td v-if="shipmentDetail != ''">{{ shipmentDetail.order.recipientAddressCity }},{{ shipmentDetail.order.recipientAddressState }},{{ shipmentDetail.order.recipientAddressCountryCode }}</td>
                          </tr>
                          <tr>
                              <td></td>
                              <td v-if="shipmentDetail != ''">{{ shipmentDetail.order.recipientZipCode }}</td>
                          </tr>
                          <tr>
                              <td><font style="color:#be2b9b">Item:</font></td>
                              <td v-if="shipmentDetail != ''">
                              {{ shipmentDetail.order.orderItems[0].itemQuantity }} X {{ shipmentDetail.order.orderItems[0].itemName }} (Qty: {{ shipmentDetail.order.itemTotalQuantity }}, Value: {{ shipmentDetail.order.orderItems[0].itemValueCurrency | currency }} {{ shipmentDetail.order.itemTotalValue  | comma }})
                              </td>                            
                          </tr>
                          <tr>
                              <td><font style="color:#be2b9b">Carrier:</font></td>
                              <td>{{ getCarrierName(shipmentDetail.carrier) }}</td>
                          </tr>
                          <tr>
                              <td><font style="color:#be2b9b">Carrier Tracking #:</font></td>
                              <td><button class="linkButton" @click="getTrckingUrl(shipmentDetail.carrier, shipmentDetail.carrierTackingNo)">{{ shipmentDetail.carrierTackingNo }}</button></td>
                          </tr>
                        </table>
                      </td>
                    </tr>             
                    <tr style="height:10px">
                        <td></td>
                    </tr>
                    <tr>
                        <td>
                          <table style="padding:10px;width:100%;background-color:#f7c3c3;">
                            <tr>
                              <td colspan="3" style="padding-bottom:10px"><h2>Tracking History</h2></td>
                            </tr>
                            <tr style="height:30px">
                              <td colspan="3">
                                <v-timeline dense clipped>
                                  <template v-for="(item, index) in this.trackingHistory">
                                    <v-timeline-item v-if="index == 0" color="blue" icon-color="grey lighten-2" small :key="index">
                                      <v-row justify="space-between">
                                        <v-col cols="3">{{ item.trackingAt | timestampToDate }}</v-col>
                                        <v-col>{{ item.eventDesc }}</v-col>
                                      </v-row>
                                    </v-timeline-item>
                                    <v-timeline-item v-else color="grey" icon-color="grey lighten-2" small :key="index">
                                      <v-row justify="space-between">
                                        <v-col cols="3">{{ item.trackingAt | timestampToDate }}</v-col>
                                        <v-col>{{ item.eventDesc }}</v-col>
                                      </v-row>
                                    </v-timeline-item>
                                  </template>
                                </v-timeline>                              
                              </td>
                            </tr>
                          </table>
                        </td>
                    </tr>
                  </table>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>            
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="primary" @click="setTrackingCancel"> OK </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <!--   -->
      
      <!--   -->
