        </template>
        <v-card>
          <v-card-text class="ma-0 pa-0">
            <v-container>
              <v-row>
                <v-col align="center" class="ma-0 pa-0">
                  <v-img alt="" contain src="@/images/otherchargetop.png" style="margin-top: 10px;width:820px"/>
                </v-col>
              </v-row>
              <v-row>
                <v-col align="center" class="ma-0 pa-0" style="margin-top: 10px;">
                  <table style="margin-top: 10px;">
                    <template  v-for="(item, index) in this.otherCharges">
                    <tr :key="index">
                      <td v-if="item.isSelect == true" style="padding-top: 5px; padding-right:10px"><v-select hide-details :items="otherChargeTypeItems" v-model="item.name" item-text="display" item-value="value" label="Type (*)" outlined dense style="width: 560px" ref="type" placeholder="Type (*)" @change="setOtherChargeTypeChange(item)"></v-select></td>
                      <td v-else style="padding-top: 5px; padding-right:10px"><v-text-field hide-details label="Type (*)" placeholder="Type (*)" outlined v-model="item.name" ref="type" dense style="width: 560px"></v-text-field></td>
                      <td style="padding-top: 5px; padding-right:10px"><v-text-field hide-details label="Amount (*)" placeholder="Amount (*)" outlined v-model="item.amount" ref="amount" dense style="width: 200px" oninput="javascript: this.value = this.value.replace(/\D/g, '' );"></v-text-field></td>
                      <td style="padding-top: 5px;"><v-img src="@/images/rubbish-bin1.png" width="25px" @click="setRemoveOtherCharge(index)" style="cursor:pointer"/></td>
                    </tr>
                    </template>
                  </table>
                </v-col>
              </v-row>
              <v-row>
                <v-col align="center">
                  <v-img alt="" contain src="@/images/otherchargeadd.png" style="width:60px;cursor:pointer" @click="setOtherChargeAdd()" />
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>            
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="error" outlined @click="setOtherChargeCancel"> Cancel </v-btn>
            <v-btn color="primary" @click="setOtherChargeOk"> OK </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <!-- Other Charge  -->      
  
      <!-- -->
      <orderDetailView :setOrderDetail="this.setOrderDetail" @close="closeOrderDetailViewDialog"></orderDetailView>      
