  .merge {
    float:left;padding:20px;page-break-inside:avoid; page-break-after:auto;
  }
  .mergeInvoice {
    float:left;padding:3px;page-break-inside:avoid; page-break-after:auto;
  }
  
  .page {
    width: 9cm;
    min-height: 14cm;
