    color: blue;
    text-decoration-line: underline;
  }
  </style>
  <script>
  import JsBarcode from "jsbarcode";
  import axios from "axios";
  import $ from "jquery";
  import common from '@/common'
  import orderDetailView from "@/components/OrderDetailView.vue"
  import VueCookies from "vue-cookies";
  import dateUtil from "@/common/DateUtil"
  import commFunction from "@/common/Function"
  import loadingBarView from "@/components/LoadingBarView.vue"
  import commonCode from "@/common/Code"

  export default {
    components: {
      orderDetailView,
      loadingBarView
    },  
    data() {
      return {
        setOrderDetail: {
          show: false,
          orderDetail: "",
          deleteBtnShow: false,
          modifyBtnShow: false,
        },
        otherCharges: [],
        otherChargeDialog: false,
        otherChargeTypeItems: commonCode.otherChargeType,
        smsResend: false,
        smsData: [],
        carrierData: "",
        itemIndex: 0,
        smsSelected: '',
        shippingMethodList: [],
        radioSelected: 'barcode',
        chkOrder: '',
        chkRecipient: '',
        barcodeDialog: false,
        shipmentDetail: [],
        trackingDialog: false,
        trackingHistory: [],
        trackingStepBackgroundColor: [],
        carrierDialog: false,
        setLoadingBar : { loadingBarDialogShow : false },
        smsDialog: false,
        actionMethod: '',
        currencyItems: common.currencyItems,
        shippingTermItems: common.shippingTermItems,
        serviceTypeItems: common.serviceTypeItems,
        packageTypeItems: common.packageTypeItems,
        contentsTypeItems: common.contentsTypeItems,
        senderAddressStateItems : [],
        recipientAddressStateItems : [],      
        orderDetail: [],
        orderDetailModifyDialog: false,
        orderDateMenu: false,
        fromDate: new Date(Date.now() - new Date().getTimezoneOffset() * 60000).toISOString().substr(0, 10),
        fromDateMenu: false,
        toDate: new Date(Date.now() - new Date().getTimezoneOffset() * 60000).toISOString().substr(0, 10),
        toDateMenu: false,
        searchText: "",
        selectedTasks: [],
        selected: [],
        headers: [
          { text: "GEM Master #", value: "order.gemMasterNo", width: "150" },
          { text: "Order #", value: "order.orderNo", width: "150" },
          { text: "Date", value: "order.orderedAt", width: "100" },
          { text: "Customer", value: "order.recipientName", width: "125" },
          { text: "Item", value: "order.itemName", width: "180" },
          { text: "GW / VW", value: "order.shipmentActualWeight", width: "125" },
          { text: "Rate", value: "order.carrier", width: "130" },
          { text: "Term", value: "order.shippingTerm", width: "86" },
          { text: "Tracking", value: "order.trackingEvent", width: "130" },
          { text: "Transit", value: "order.transitDays", width: "100" },
          { text: "Type", value: "order.serviceType", width: "86" },
          { text: "", value: "" },
        ],
        desserts: [],
        main: [],
        currencyCount: 0,
      };
    },
    filters: {       
    },
    computed: {
    },
    methods: {
      setOtherChargeTypeChange(item) {
        if(item.name == "ETC") {
          /* ETC INPUT BOX  
          item.isSelect = false;
          item.name = "";
          */
        }
      },
      setOtherChargeAdd() {
        var otherCharge = new Object();
        otherCharge.name = '';
        otherCharge.amount = '';
        otherCharge.isSelect = true;
        this.otherCharges.push(otherCharge);
      },
      setRemoveOtherCharge(index) {
        this.otherCharges.splice(index, 1);
      },
      setOtherChargeCancel() {
        this.otherChargeDialog = false;
      },
      setOtherChargeOk() {        
        var vm = this;
        var totalOtherChargeAmount = 0;
        var totalAmount = 0;
        vm.setLoadingBar.loadingBarDialogShow = true;
        for(var index=0; index<this.otherCharges.length; index++) {
          if(this.otherCharges[index].name == "") {
            vm.$alert(common.messages.otherChargeTypeCheck, "", "info").then(() => {});
            return false;
          }
          if(this.otherCharges[index].amount == "") {
            vm.$alert(common.messages.otherChargeAmountCheck, "", "info").then(() => {});
            return false;
          }
          totalOtherChargeAmount += Number(this.otherCharges[index].amount);
        }

        //
        this.carrierData.orderCharges = this.carrierData.charges;

        for(var orderChargIndex = 0; orderChargIndex < this.carrierData.orderCharges.length; orderChargIndex++)
        {
          var charge = new Object(); 

          charge.carrierAmount = this.carrierData.orderCharges[orderChargIndex].carrierAmount;
          charge.fuelAmount = this.carrierData.orderCharges[orderChargIndex].fuelAmount;
          charge.markupAmount = this.carrierData.orderCharges[orderChargIndex].markupAmount;
          charge.handlingAmount = this.carrierData.orderCharges[orderChargIndex].handlingAmount;
          charge.totalSurchargeAmount = this.carrierData.orderCharges[orderChargIndex].surchargeAmount;
          charge.totalOtherChargeAmount = totalOtherChargeAmount;

          charge.totalAmount = this.carrierData.orderCharges[orderChargIndex].totalAmount;
          charge.paymentAmount = Number(charge.totalAmount) + Number(charge.totalOtherChargeAmount);

          this.carrierData.orderCharges[orderChargIndex].charge = charge;
          this.carrierData.orderCharges[orderChargIndex].otherCharges = this.otherCharges;

          totalAmount += Number(charge.paymentAmount);
        }

        this.carrierData.totalAmount = totalAmount;
        //

        axios
        .post(common.makeAPIUrl("/payments/request"), this.carrierData, {
          headers: { "Content-Type": "application/json", Authorization: vm.$session.get("authorization") },
        })
        .then(() => {
          this.setSmsSend();
        })
        .catch((err) => {
          vm.setLoadingBar.loadingBarDialogShow = false;
          vm.$alert(err.response.data.message, "", "info").then(() => { this.otherChargeDialog = false; this.getDataInit(); });
        });

      },
      setSmsResend(item) {
        this.smsDialog = true;
        this.smsResend = true;
        this.smsData.senderName = item.order.senderName;
