      setShippngMethod(item) {
        if(Number(item.totalValue) <= 0) {
          this.$alert(common.messages.shippingMethodCheck, "", "info").then(() => {});
          return false;
        }
        this.smsSelected = "";
        this.carrierData = "";
        
        var loginId = this.$session.get("loginId");
        this.setLoadingBar.loadingBarDialogShow = true;
  
        var data = new Object();
        data.carrierService = item.carrierService;
        var totalWeight = 0;
        var charges = [];
  
        for (var itemRateCount = 0; itemRateCount < item.rates.length; itemRateCount++) {
          for (var selectedTasksCount = 0; selectedTasksCount < this.selectedTasks.length; selectedTasksCount++) { 
            if(this.selectedTasks[selectedTasksCount].id == item.rates[itemRateCount].shipmentId) {
              data.currency = "KRW";
              data.gemMasterNo = this.selectedTasks[0].order.gemMasterNo;
              data.orderCount = this.selectedTasks.length;
              data.sellerId = loginId;
              data.totalAmount = item.totalValue;

              var charge = new Object();
              charge.carrier = item.rates[itemRateCount].carrier;
              charge.carrierAmount = item.rates[itemRateCount].carrierValue;
              charge.fuelAmount = item.rates[itemRateCount].fuelValue;
              charge.handlingAmount = item.rates[itemRateCount].handlingCost;
              charge.markupAmount = item.rates[itemRateCount].marginValue;
              charge.orderId = this.selectedTasks[selectedTasksCount].order.id;
              charge.surchargeAmount = item.rates[itemRateCount].surchargeValue;
              charge.totalAmount = item.rates[itemRateCount].totalValue;
              charge.weight = (Number(this.selectedTasks[selectedTasksCount].order.shipmentActualWeight) > Number(this.selectedTasks[selectedTasksCount].order.shipmentVolumeWeight)) ? this.selectedTasks[selectedTasksCount].order.shipmentActualWeight :  this.selectedTasks[selectedTasksCount].order.shipmentVolumeWeight;
              charge.surcharges = [];
  
              for (var surchargeCount = 0; surchargeCount < item.rates[itemRateCount].surcharges.length; surchargeCount++) {
                var surcharge = new Object();
                surcharge.amount = item.rates[itemRateCount].surcharges[surchargeCount].value;
                surcharge.name = item.rates[itemRateCount].surcharges[surchargeCount].name;
  
                charge.surcharges.push(surcharge);
              }            
  
              totalWeight = Number(totalWeight) + Number(charge.weight);
  
              charges.push(charge);
            }
          }
        }
        data.totalWeight = totalWeight;
        data.charges = charges;

        this.carrierData = data;

        this.smsData.senderName = this.selectedTasks[0].order.senderName;
        this.smsData.senderMobile =  this.selectedTasks[0].order.senderMobileNo;
        this.smsData.recipientName =  this.selectedTasks[0].order.recipientName;
        this.smsData.recipientMobile =  this.selectedTasks[0].order.recipientMobileNo;

        this.smsDialog = true;
        this.carrierDialog = false;
        this.setLoadingBar.loadingBarDialogShow = false;
      },
      
      setBarcodePrint() {
        var orderIds = [];
        var reqeustURL = "";
        reqeustURL = "print-label";
        
        for (var orderCount = 0; orderCount < this.selectedTasks.length; orderCount++) {
          if(this.selectedTasks[orderCount].isPrintBarcode != "Y") {
            orderIds.push(this.selectedTasks[orderCount].order.id);
          }
        }
        if(orderIds.length != 0) {
          axios.put(common.makeAPIUrl('/sellers/' + this.$session.get('loginId') + "/orders/" + reqeustURL), orderIds, 
          { headers: {'Authorization': this.$session.get('authorization'), "Content-Type": "application/json"}})
          .then(response => {                
            if(response.status == 200) {
              for (var orderCount = 0; orderCount < this.selectedTasks.length; orderCount++) {
                this.selectedTasks[orderCount].isPrintBarcode = "Y";
                this.barcodeDialog = false;
              }
            }
          })
          .catch(err => {
            this.$alert(err.response.data.message, '', 'info').then(() => { })
          })        
        }
      },
      setMultiDelete() {
        this.selectedTasks = this.selected;
        if(this.selectedTasks == "") {
          this.$alert(common.messages.shipmentSelect, '', 'info').then(() => {  })
          return false;
        }
        var dataList = [];
        for (let selectedCount=0; selectedCount<this.selectedTasks.length; selectedCount++) {
          if (this.selectedTasks[selectedCount].isPrintAWB == 'Y') {
            this.$alert("AWB Printed", "", "info").then(() => { this.selectedTasks = []; });
            return false;
          } else {
            dataList.push(this.selectedTasks[selectedCount].id);
          }
        }
        this.$confirm(common.messages.shipmetDelete, "", "question").then(() => {
          this.setLoadingBar.loadingBarDialogShow = true;
          var loginId = this.$session.get("loginId");
          var authorization = this.$session.get("authorization");
          var vm = this;
  
          var config = {
            method: "delete",
            url: common.makeAPIUrl("/sellers/" + loginId + "/shipments"),
            headers: {
              Authorization: authorization,
              "Content-Type": "application/json",
            },
            data: dataList,
          };
  
          axios(config)
            .then(function (response) {
              if (response.status == 200) {
                vm.$alert(common.messages.shipmentDeleteComplate, "", "info").then(() => {
                  vm.setLoadingBar.loadingBarDialogShow = false;
                  vm.getDataInit();
                });
              }
            })
            .catch(function (err) {
              vm.selectedTasks = [];
              vm.selected = [];
              vm.setLoadingBar.loadingBarDialogShow = false;
              vm.$alert(err.response.data.message, "", "info").then(() => {});
            });        
        });
  
      },
