      getDataInit() {
        document.querySelector('.v-data-table__wrapper').scrollTo({ left: 0, top: 0 });
        this.setLoadingBar.loadingBarDialogShow = true;
        axios.get(common.makeAPIUrl('/sellers/' + this.$session.get('loginId') + '/shipments?startDate=' + this.fromDate + '&endDate=' + this.toDate + '&keyword=' + this.searchText + '')
        , { headers: {'Authorization': this.$session.get('authorization')}})
        .then(response => {
          this.main = response.data;
          for(var orderCount = 0; orderCount < this.main.length; orderCount++)
          {
            this.main[orderCount].order.orderedAt = common.getYYYMMDD(this.main[orderCount].order.orderedAt)
            this.main[orderCount].order.bookingNo = "";
            this.main[orderCount].order.trackingEventAbbreviation = commonCode.getTrackingEventAbbreviation(this.main[orderCount].order.trackingEvent, this.main[orderCount].order.recipientAddressCountryCode, this.main[orderCount].order.recipientAddressCity);
          }

          this.desserts = [];
          this.selectedTasks = [];
          this.selected = [];
          this.currencyCount = 0;
          this.setLoadingBar.loadingBarDialogShow = false;
          this.smsDialog = false;
          $('input[name="allCheck"]').prop('checked', false);
          this.getDataList();
        })
        .catch(err => {
          if(err.response.status == 401) {
            this.$alert(common.messages.login, "", "info").then(() => {
              this.$router.push("/");
            });
            return false;
          }
          this.$alert(err.response.data.message, '', 'info').then(() => {  })
          this.setLoadingBar.loadingBarDialogShow = false;
        })
      },
