          //Barcode  
          if(this.selectedTasks[selectedCount].isPrintBarcode != "Y") {
            this.$alert(common.messages.barcodePrintCheck, "", "info").then(() => { this.selectedTasks = []; });
            return false;
          }
          //AWB  
          if(this.selectedTasks[selectedCount].isPrintAWB != "Y") {
            this.$alert(common.messages.awbPrintCheck, "", "info").then(() => { this.selectedTasks = []; });
            return false;
          }
          //FEDEX 
          if(this.selectedTasks[selectedCount].carrier == "FEDEX_FICP" || this.selectedTasks[selectedCount].carrier == "FEDEX_IP") {
            this.$alert(common.messages.AWBPrintReCheck, "", "info").then(() => { this.selectedTasks = []; });
