      onChkPrint(checked) {
        if(checked) {
          if (this.radioSelected == "barcode") {
            $('.pageTemp').addClass('page');
            $('.pageTemp').removeClass('pageTemp');
            $('.subPage').removeClass('merge');
          }
          if (this.radioSelected == "invoice") {
            $('.pageTemp').addClass('page');
            $('.pageTemp').removeClass('pageTemp');
            $('.subPage').removeClass('mergeInvoice');
          }
        } else {
          if (this.radioSelected == "barcode") {
            $('.page').addClass('pageTemp');
            $('.page').removeClass('page');
            $('.subPage').addClass('merge');
          }
          if (this.radioSelected == "invoice") {
            $('.page').addClass('pageTemp');
            $('.page').removeClass('page');
            $('.subPage').addClass('mergeInvoice');
          }        
        }
      },
