      trackingView(row) {
        this.shipmentDetail = row;
        this.shipmentDetail.trackingEventAbbreviation = commonCode.getTrackingEventAbbreviation(this.shipmentDetail.trackingEvent, row.order.recipientAddressCountryCode, row.order.recipientAddressCity);
  
        axios.get(common.makeAPIUrl('/orders/' + row.order.id + '/tracking'), { headers: {'Authorization': this.$session.get('authorization')}})
        .then(response => {
          this.trackingHistory = response.data;
          for(let index=0; index<this.trackingHistory.length; index++) {
            // Desc 
            this.trackingHistory[index].eventDesc = commonCode.getTrackingEventDesc(this.trackingHistory[index].event, this.trackingHistory[index].city, this.trackingHistory[index].countryCode);
            this.trackingHistory[index].createdAt = common.getYYYMMDDHHMMSS(this.trackingHistory[index].createdAt)
          }
          this.trackingStepBackgroundColor = common.trackingStepBackgroundColor(response.data);
          this.trackingDialog = true
        })
        .catch(err => {
          this.$alert(err.response.data.message, '', 'info').then(() => {  })
        })
