      carrierSelect() {
        this.setLoadingBar.loadingBarDialogShow = true;
        this.smsResend = false;
        this.selectedTasks = this.selected;
        if (this.selectedTasks.length == 0) {
          this.setLoadingBar.loadingBarDialogShow = false;
          this.$alert(common.messages.shipmentSelect, "", "info").then(() => {});
          return false;
        } else if (this.selectedTasks.length > 1) {
          this.setLoadingBar.loadingBarDialogShow = false;
          this.$alert(common.messages.selectOnlyOne, "", "info").then(() => {});
          return false;    
        } else {
          var shipmentIds = [];
          for (var orderCount = 0; orderCount < this.selectedTasks.length; orderCount++) {
            if(this.selectedTasks[orderCount].order.isPrintAWB == "Y") {
              this.setLoadingBar.loadingBarDialogShow = false;
              this.$alert(common.messages.AWBPrinted, '', 'info').then(() => {  })  
              return false;
            }
            if(this.selectedTasks[orderCount].order.paymentStatus == "COMPLETE") {
              this.setLoadingBar.loadingBarDialogShow = false;
              this.$alert(common.messages.paymentStatusCheck, '', 'info').then(() => {  })  
              return false;
            }
            shipmentIds.push(this.selectedTasks[orderCount].id);
          }
          axios.post(common.makeAPIUrl('/sellers/' + this.$session.get('loginId') + '/shipments/calculate'), shipmentIds, 
          { headers: {'Authorization': this.$session.get('authorization'), "Content-Type": "application/json"}})
          .then(response => {                
            if(response.status == 200) {
              this.setLoadingBar.loadingBarDialogShow = false;
              this.shippingMethodList = response.data;
              this.carrierDialog = true;
            }
          })
          .catch(err => {
            this.setLoadingBar.loadingBarDialogShow = false;
            this.$alert(err.response.data.message, '', 'info').then(() => {  })
          })        
        }
