      setOrderDetailModifyOk() {
        let checkEmail = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
        let checkFloat =  /^[0-9]+(.[0-9]+)?$/;
        let checkNumber = /^[0-9]*$/;
  
        if(this.orderDetail.orderNo == null || this.orderDetail.orderNo == '') { this.$alert(common.messages.orderNo, "", "info").then(() => { this.$refs.orderNo.focus(); }); return false; }
        if(this.orderDetail.orderNo.length > 26) { this.$alert(common.messages.orderNo26, '', 'info').then(() => { this.$refs.orderNo.focus(); }); return false; }
        
        if(this.orderDetail.storePlatform == null || this.orderDetail.storePlatform == '') { this.$alert(common.messages.storePlatform, "", "info").then(() => { this.$refs.storePlatform.focus(); }); return false; }
        if(this.orderDetail.storePlatform.length > 30) { this.$alert(common.messages.storePlatform30, '', 'info').then(() => { this.$refs.storePlatform.focus(); }); return false; }
  
        if(this.orderDetail.storeName == null || this.orderDetail.storeName == '') { this.$alert(common.messages.storeName, "", "info").then(() => { this.$refs.storeName.focus(); }); return false; }
        if(this.orderDetail.storeName.length > 30) { this.$alert(common.messages.storeName30, '', 'info').then(() => { this.$refs.storeName.focus(); }); return false; }
  
        if(this.orderDetail.orderedAt == null || this.orderDetail.orderedAt == '') { this.$alert(common.messages.orderedAt, "", "info").then(() => { this.$refs.orderedAt.focus(); }); return false; }
  
        if(this.orderDetail.shippingTerm == null || this.orderDetail.shippingTerm == '') { this.$alert(common.messages.shippingTerm, "", "info").then(() => { this.$refs.shippingTerm.focus(); }); return false; }
  
        if(this.orderDetail.itemContentsType == null || this.orderDetail.itemContentsType == '') { this.$alert(common.messages.itemContentsType, "", "info").then(() => { this.$refs.itemContentsType.focus(); }); return false; }
  
        if(this.orderDetail.senderName == null || this.orderDetail.senderName == '') { this.$alert(common.messages.senderName, "", "info").then(() => { this.$refs.senderName.focus(); }); return false; }
        if(this.orderDetail.senderName.length > 23) { this.$alert(common.messages.senderName23, '', 'info').then(() => { this.$refs.senderName.focus(); }); return false; }
        
        if(this.orderDetail.senderEmail == null || this.orderDetail.senderEmail == '') { this.$alert(common.messages.senderEmail, "", "info").then(() => { this.$refs.senderEmail.focus(); }); return false; }
        if(this.orderDetail.senderEmail.length > 50) { this.$alert(common.messages.senderEmail50, '', 'info').then(() => { this.$refs.senderEmail.focus(); }); return false; }
        if(!checkEmail.test(String(this.orderDetail.senderEmail))) { this.$alert(common.messages.senderEmailFilter, '', 'info').then(() => { this.$refs.senderEmail.focus(); }); return false; }
  
        if(this.orderDetail.senderPhoneNo == null || this.orderDetail.senderPhoneNo == '') { this.$alert(common.messages.senderPhoneNo, "", "info").then(() => { this.$refs.senderPhoneNo.focus(); }); return false; }
        if(this.orderDetail.senderPhoneNo.length > 30) { this.$alert(common.messages.senderPhoneNo30, '', 'info').then(() => { this.$refs.senderPhoneNo.focus(); }); return false;  }
  
        if(this.orderDetail.senderMobileNo == null || this.orderDetail.senderMobileNo == '') { this.$alert(common.messages.senderMobileNo, "", "info").then(() => { this.$refs.senderMobileNo.focus(); }); return false; }
        if(this.orderDetail.senderMobileNo.length > 30) { this.$alert(common.messages.senderMobileNo30, '', 'info').then(() => { this.$refs.senderMobileNo.focus(); }); return false; }
  
        if(this.orderDetail.senderAddress1 == null || this.orderDetail.senderAddress1 == '') { this.$alert(common.messages.senderAddress1, "", "info").then(() => { this.$refs.senderAddress1.focus(); }); return false; }
        if((this.orderDetail.senderAddress1 + " " + this.orderDetail.senderAddress2).length > 69) { this.$alert(common.messages.senderAddress68, '', 'info').then(() => { this.$refs.senderAddress1.focus(); }); return false; }
  
        if(this.orderDetail.senderAddressCity == null || this.orderDetail.senderAddressCity == '') { this.$alert(common.messages.senderAddressCity, "", "info").then(() => { this.$refs.senderAddressCity.focus(); }); return false; }
        if(this.orderDetail.senderAddressCity.length > 30) { this.$alert(common.messages.senderAddressCity30, '', 'info').then(() => { this.$refs.senderAddressCity.focus(); }); return false; }
  
        if(this.orderDetail.senderAddressCountryCode.toUpperCase() == 'CA' || this.orderDetail.senderAddressCountryCode.toUpperCase() == 'IN' || 
        this.orderDetail.senderAddressCountryCode.toUpperCase() == 'MX' || this.orderDetail.senderAddressCountryCode.toUpperCase() == 'US' || this.orderDetail.senderAddressCountryCode.toUpperCase() == 'AE')
        {
          if(this.orderDetail.senderAddressStateCode == null || this.orderDetail.senderAddressStateCode == '') { this.$alert(common.messages.senderAddressState, "", "info").then(() => { this.$refs.senderAddressStateCode.focus(); }); return false; }
          if(this.orderDetail.senderAddressState == null || this.orderDetail.senderAddressState == '') { this.$alert(common.messages.senderAddressState, "", "info").then(() => { this.$refs.senderAddressState.focus(); }); return false; }
          if(!commFunction.stateCodeCheck(this.orderDetail.senderAddressCountryCode.toUpperCase(), this.orderDetail.senderAddressState.toUpperCase())) {
            this.$alert(common.messages.senderAddressState, '', 'info').then(() => { this.$refs.senderAddressState.focus(); }); return false;
          }
        } 
  
        if((this.orderDetail.senderAddressCity + " " + this.orderDetail.senderAddressState).length > 46) { this.$alert(common.messages.senderCity45, '', 'info').then(() => { this.$refs.senderAddressCity.focus(); }); return false; }
  
        if(this.orderDetail.senderZipCode == null || this.orderDetail.senderZipCode == '') { this.$alert(common.messages.senderZipCode, "", "info").then(() => { this.$refs.senderZipCode.focus(); }); return false; }
        if(this.orderDetail.senderZipCode.length > 20) { this.$alert(common.messages.senderZipCode20, '', 'info').then(() => { this.$refs.senderZipCode.focus(); }); return false; }
  
        if(this.orderDetail.senderAddressCountryCode == null || this.orderDetail.senderAddressCountryCode == '') { this.$alert(common.messages.senderAddressCountryCode, "", "info").then(() => { this.$refs.senderAddressCountryCode.focus(); }); return false; }
  
        if(this.orderDetail.recipientName == null || this.orderDetail.recipientName == '') { this.$alert(common.messages.recipientName, "", "info").then(() => { this.$refs.recipientName.focus(); }); return false; }
        if(this.orderDetail.recipientName.length > 23) { this.$alert(common.messages.recipientName23, '', 'info').then(() => { this.$refs.recipientName.focus(); }); return false; }
        
        if(this.orderDetail.recipientEmail == null || this.orderDetail.recipientEmail == '') { this.$alert(common.messages.recipientEmail, "", "info").then(() => { this.$refs.recipientEmail.focus(); }); return false; }
        if(this.orderDetail.recipientEmail.length > 50) { this.$alert(common.messages.recipientEmail50, '', 'info').then(() => { this.$refs.recipientEmail.focus(); }); return false; }
        if(!checkEmail.test(String(this.orderDetail.recipientEmail))) { this.$alert(common.messages.recipientEmailFilter, '', 'info').then(() => { this.$refs.recipientEmail.focus(); }); return false; }
  
        if(this.orderDetail.recipientPhoneNo == null || this.orderDetail.recipientPhoneNo == '') { this.$alert(common.messages.recipientPhoneNo, "", "info").then(() => { this.$refs.recipientPhoneNo.focus(); }); return false; }
        if(this.orderDetail.recipientPhoneNo.length > 30) { this.$alert(common.messages.recipientPhoneNo30, '', 'info').then(() => { this.$refs.recipientPhoneNo.focus(); }); return false;  }
  
        if(this.orderDetail.recipientMobileNo == null || this.orderDetail.recipientMobileNo == '') { this.$alert(common.messages.recipientMobileNo, "", "info").then(() => { this.$refs.recipientMobileNo.focus(); }); return false; }
        if(this.orderDetail.recipientMobileNo.length > 30) { this.$alert(common.messages.recipientMobileNo30, '', 'info').then(() => { this.$refs.recipientMobileNo.focus(); }); return false; }
  
        if(this.orderDetail.recipientAddress1 == null || this.orderDetail.recipientAddress1 == '') { this.$alert(common.messages.recipientAddress1, "", "info").then(() => { this.$refs.recipientAddress1.focus(); }); return false; }
        if((this.orderDetail.recipientAddress1 + " " + this.orderDetail.recipientAddress2).length > 69) { this.$alert(common.messages.recipientAddress68, '', 'info').then(() => { this.$refs.recipientAddress1.focus(); }); return false; }
  
        if(this.orderDetail.recipientAddressCity == null || this.orderDetail.recipientAddressCity == '') { this.$alert(common.messages.recipientAddressCity, "", "info").then(() => { this.$refs.recipientAddressCity.focus(); }); return false; }
        if(this.orderDetail.recipientAddressCity.length > 30) { this.$alert(common.messages.recipientAddressCity30, '', 'info').then(() => { this.$refs.recipientAddressCity.focus(); }); return false; }
  
        if(this.orderDetail.recipientAddressCountryCode.toUpperCase() == 'CA' || this.orderDetail.recipientAddressCountryCode.toUpperCase() == 'IN' || 
        this.orderDetail.recipientAddressCountryCode.toUpperCase() == 'MX' || this.orderDetail.recipientAddressCountryCode.toUpperCase() == 'US' || this.orderDetail.recipientAddressCountryCode.toUpperCase() == 'AE')
        {
          if(this.orderDetail.recipientAddressStateCode == null || this.orderDetail.recipientAddressStateCode == '') { this.$alert(common.messages.recipientAddressState, "", "info").then(() => { this.$refs.recipientAddressStateCode.focus(); }); return false; }
          if(this.orderDetail.recipientAddressState == null || this.orderDetail.recipientAddressState == '') { this.$alert(common.messages.recipientAddressState, "", "info").then(() => { this.$refs.recipientAddressState.focus(); }); return false; }
          if(!commFunction.stateCodeCheck(this.orderDetail.recipientAddressCountryCode.toUpperCase(), this.orderDetail.recipientAddressStateCode.toUpperCase())) {
            this.$alert(common.messages.recipientAddressState, '', 'info').then(() => { this.$refs.recipientAddressStateCode.focus(); }); return false;
          }
        }
        
        if((this.orderDetail.recipientAddressCity + " " + this.orderDetail.recipientAddressState).length > 46) { this.$alert(common.messages.recipientCity45, '', 'info').then(() => { this.$refs.recipientAddressCity.focus(); }); return false; }
  
        if(this.orderDetail.recipientZipCode == null || this.orderDetail.recipientZipCode == '') { this.$alert(common.messages.recipientZipCode, "", "info").then(() => { this.$refs.recipientZipCode.focus(); }); return false; }
        if(this.orderDetail.recipientZipCode.length > 20) { this.$alert(common.messages.recipientZipCode20, '', 'info').then(() => { this.$refs.recipientZipCode.focus(); }); return false; }
  
        if(this.orderDetail.recipientAddressCountryCode == null || this.orderDetail.recipientAddressCountryCode == '') { this.$alert(common.messages.recipientAddressCountryCode, "", "info").then(() => { this.$refs.recipientAddressCountryCode.focus(); }); return false; }
  
        if(this.orderDetail.shipmentActualWeight == null || this.orderDetail.shipmentActualWeight == '') { this.$alert(common.messages.shipmentActualWeight, "", "info").then(() => { this.$refs.shipmentActualWeight.focus(); }); return false; }
        if(!checkFloat.test(String(this.orderDetail.shipmentActualWeight))) { this.$alert(common.messages.shipmentActualWeightFloat, '', 'info').then(() => { this.$refs.shipmentActualWeight.focus(); }); return false; }
  
        if(this.orderDetail.shipmentVolumeWeight == null || this.orderDetail.shipmentVolumeWeight == '') { this.$alert(common.messages.shipmentVolumeWeight, "", "info").then(() => { this.$refs.shipmentVolumeWeight.focus(); }); return false; }
        if(!checkFloat.test(String(this.orderDetail.shipmentVolumeWeight))) { this.$alert(common.messages.shipmentVolumeWeightFloat, '', 'info').then(() => { this.$refs.shipmentVolumeWeight.focus(); }); return false; }
  
        if(this.orderDetail.weightUnit != null && this.orderDetail.weightUnit != "" && this.orderDetail.weightUnit.length > 10) { this.$alert(common.messages.weightUnit, '', 'info').then(() => { this.$refs.weightUnit.focus(); }); return false; }
    
        if(this.orderDetail.dimensionWidth != "" && !checkFloat.test(String(this.orderDetail.dimensionWidth))) { this.$alert(common.messages.width, '', 'info').then(() => { this.$refs.dimensionWidth.focus(); }); return false; }
        if(this.orderDetail.dimensionLength != "" && !checkFloat.test(String(this.orderDetail.dimensionLength))) { this.$alert(common.messages.length, '', 'info').then(() => { this.$refs.dimensionLength.focus(); }); return false; }
        if(this.orderDetail.dimensionHeight != "" && !checkFloat.test(String(this.orderDetail.dimensionHeight))) { this.$alert(common.messages.height, '', 'info').then(() => { this.$refs.dimensionHeight.focus(); }); return false; }
  
        if(this.orderDetail.parcelCount == null || this.orderDetail.parcelCount == '' || this.orderDetail.parcelCount == 0) { this.$alert(common.messages.parcelCount, "", "info").then(() => { this.$refs.parcelCount.focus(); }); return false; }
  
        if (this.orderDetail.orderItems.length <= 0) { this.$alert(common.messages.orderItemsLength, "", "info").then(() => { }); return false; }
  
        if((this.orderDetail.recipientAddressCountryCode == "RU" || this.orderDetail.recipientAddressCountryCode == "GE" || this.orderDetail.recipientAddressCountryCode == "AM")
         && (this.orderDetail.serviceType == null || this.orderDetail.serviceType == "")) { this.$alert(common.messages.serviceType, '', 'info').then(() => { this.$refs.serviceType.focus(); }); return false; }
        if(this.orderDetail.recipientAddressCountryCode == "RU" || this.orderDetail.recipientAddressCountryCode == "GE" || this.orderDetail.recipientAddressCountryCode == "AM") {
          if(this.orderDetail.serviceType.toUpperCase() == 'D2D' || this.orderDetail.serviceType.toUpperCase() == 'POD') { true; }
          else { this.$alert(common.messages.serviceTypeFilter, '', 'info').then(() => { this.$refs.serviceType.focus(); }); return false; }
        }
  
        for(var itemsCnt=0; itemsCnt<this.orderDetail.orderItems.length; itemsCnt++)
        {
          if(this.orderDetail.orderItems[itemsCnt].itemName == null || this.orderDetail.orderItems[itemsCnt].itemName == "") { this.$alert(common.messages.itemName, "", "info").then(() => { }); return false; }
          
          if(this.orderDetail.orderItems[itemsCnt].itemName.length > 100) { this.$alert(common.messages.itemNameLength + "(" + this.orderDetail.orderItems[itemsCnt].itemName + ")", "", "info").then(() => { }); return false; }
  
          if(this.orderDetail.orderItems[itemsCnt].itemQuantity == null || this.orderDetail.orderItems[itemsCnt].itemQuantity == "") { this.$alert(common.messages.itemQuantity, "", "info").then(() => { }); return false; }
          if(!checkNumber.test(this.orderDetail.orderItems[itemsCnt].itemQuantity)) { this.$alert(common.messages.itemQuantityNumber, '', 'info').then(() => { }); return false; }
  
          if(this.orderDetail.orderItems[itemsCnt].itemUnitValue == null || this.orderDetail.orderItems[itemsCnt].itemUnitValue == "") { this.$alert(common.messages.itemUnitValue, "", "info").then(() => { }); return false; }
          if(!checkFloat.test(this.orderDetail.orderItems[itemsCnt].itemUnitValue)) { this.$alert(common.messages.itemUnitValueFloat, '', 'info').then(() => { }); return false; }
  
          if (this.orderDetail.orderItems[itemsCnt].itemValueCurrency == null || this.orderDetail.orderItems[itemsCnt].itemValueCurrency == "") { this.$alert(common.messages.itemValueCurrency, "", "info").then(() => { }); return false; }
  
          var defaultCurrenty = this.orderDetail.orderItems[0].itemValueCurrency
  
          if (defaultCurrenty != this.orderDetail.orderItems[itemsCnt].itemValueCurrency) { this.$alert(common.messages.itemValueCurrency, "", "info").then(() => { }); return false; }
          
          if(this.orderDetail.orderItems[itemsCnt].itemWeight == null || this.orderDetail.orderItems[itemsCnt].itemWeight == "") { this.$alert(common.messages.itemWeight, "", "info").then(() => { }); return false; }
          if(!checkFloat.test(this.orderDetail.orderItems[itemsCnt].itemWeight)) { this.$alert(common.messages.itemWeightFloat, '', 'info').then(() => { }); return false; }
  
          if(this.orderDetail.orderItems[itemsCnt].itemHSCode == null || this.orderDetail.orderItems[itemsCnt].itemHSCode == "") { this.$alert(common.messages.itemHSCode, "", "info").then(() => { }); return false; }
          if(!checkFloat.test(this.orderDetail.orderItems[itemsCnt].itemHSCode)) { this.$alert(common.messages.itemHSCodeFloat, '', 'info').then(() => { }); return false; }
  
          if(this.orderDetail.orderItems[itemsCnt].itemOriginCountryCode == null || this.orderDetail.orderItems[itemsCnt].itemOriginCountryCode == "") { this.$alert(common.messages.itemOriginCountryCode, "", "info").then(() => { }); return false; }
  
          if((this.orderDetail.recipientAddressCountryCode == "RU" || this.orderDetail.recipientAddressCountryCode == "GE" || this.orderDetail.recipientAddressCountryCode == "AM")
           && (this.orderDetail.orderItems[itemsCnt].itemUrl == null || this.orderDetail.orderItems[itemsCnt].itemUrl == "")) { this.$alert(common.messages.itemSalesURL, '', 'info').then(() => { }); return false; }
    
          if(this.orderDetail.orderItems[itemsCnt].itemBarcode != null && this.orderDetail.orderItems[itemsCnt].itemBarcode != "" && this.orderDetail.orderItems[itemsCnt].itemBarcode.length > 30) { this.$alert(common.messages.itemBarcode30, '', 'info').then(() => { }); return false; }
    
          if(this.orderDetail.orderItems[itemsCnt].itemSKU != null && this.orderDetail.orderItems[itemsCnt].itemSKU != "" && this.orderDetail.orderItems[itemsCnt].itemSKU.length > 255) { this.$alert(common.messages.itemSKU255, '', 'info').then(() => { }); return false; }
    
          if (this.orderDetail.orderItems[itemsCnt].parcelNo == null || this.orderDetail.orderItems[itemsCnt].parcelNo == "") { this.$alert(common.messages.itemParcelIndex, "", "info").then(() => { }); return false; }
          if (this.orderDetail.parcelCount < this.orderDetail.orderItems[itemsCnt].parcelNo) { this.$alert(common.messages.parcelCountIndex, "", "info").then(() => { }); return false; }
        }
  
        var loginId = this.$session.get("loginId");
        var authorization = this.$session.get("authorization");
  
        if(this.actionMethod == "Modify")
        {
          axios
          .put(common.makeAPIUrl("/sellers/" + loginId + "/orders/" + this.orderDetail.id), this.orderDetail, {
            headers: { "Content-Type": "application/json", Authorization: authorization },
          })
          .then((response) => {
            if (response.status == 200) {
              this.$alert(common.messages.orderModifyComplate, "", "info").then(() => {});
              this.orderDetailModifyDialog = false;
              this.getDataInit();
            }
          })
          .catch((err) => {
            this.$alert(err.response.data.message, "", "info").then(() => {});
          });
        } else if(this.actionMethod == "Add") {
          axios.post(common.makeAPIUrl('/sellers/' + this.$session.get('loginId') + '/orders'), this.orderDetail, { headers: {'Authorization': this.$session.get('authorization')}})
          .then(response => {                
            if(response.status == 200) {
              this.$alert(common.messages.orderNewComplate, '', 'info').then(() => {  })
              this.orderDetailModifyDialog = false
              this.getDataInit()
            }
          })
          .catch(err => {
            this.$alert(err.response.data.message, '', 'info').then(() => {  })
          })
        } else {
          this.$alert("System Method Error.", "", "info").then(() => {});
        }
      },
      orderDetailModify(row) {
        if(row.order.paymentStatus == "COMPLETE" || row.order.paymentStatus == "WAIT") {
          this.$alert(common.messages.paymentStatusCheck, "", "info").then(() => {});
          return false;
        }
        if(row.isPrintAWB == 'Y') {
          this.$alert(common.messages.AWBPrinted, "", "info").then(() => {});
          return false;
        }
        this.actionMethod = 'Modify'
        this.orderDetail = JSON.parse(JSON.stringify(row.order));
        this.orderDetailModifyDialog = true
      },
      setSearch() {
        this.getDataInit()
      },
      getPlatformImgUrl(value) {
        return common.getPlatformImgUrl(value)
      },
      getRateImgUrl(value) {
        return common.getRateImgUrl(value)
      },
      getCarrierServiceImage(value) {
        return common.getCarrierServiceImage(value)
      },
      getCarrierName(value) {
      return common.getCarrierName(value)
      },
      getCountry(value) {
        return common.getCountry(value)
      },
      getTrckingUrl(value, TN){
      return window.open(common.getTrckingUrl(value, TN),'_blank')
    },
    },
    mounted() {
      if (this.$session.get("loginId") == "" || this.$session.get("authorization") == "") {
        this.$alert(common.messages.login, "", "info").then(() => {
          this.$router.push("/");
        });
        return false;
      }
          
      $(document).prop("title", "Shipping Management");
      $("#bar_header").attr("style", "background-color:#E2E1F1");
      $("#top_header").show();
      $("#topNaviText").text("Shipping Management");        
  
      this.fromDate = dateUtil.fromYYYYmmdd();
      this.toDate = dateUtil.toYYYYmmdd();
  
      var dataTableHeight = "height:" + String(window.innerHeight - 280) + "px";
      this.getDataInit();
      $("#dataTableContent").children().attr("style", dataTableHeight);
      $(".v-data-table-header>").prepend("<th style='width:15px'><input type='checkbox' style='width:20px;height:20px' id='allCheck' name='allCheck'></th>")
  
      this.targetAllCheck = document.querySelector('#allCheck');
      this.targetAllCheck.addEventListener('change', this.setAllCheck);
    },
    updated() {
      $(".v-data-table-header>tr>th").css({'background-color':'#BAE8B6' });
      $(".v-data-table-header>tr>th").css({'text-align':'center' });
      $(".v-data-table-header>tr>th").css({'font-size':'15px' });
      $(".v-data-table-header>tr>th").css({'font-style':'bold' });
  
      JsBarcode(".barcode").init();
      
      $("#divBarcode").hide();
      $("#btnBarcodePrint").hide();
      $("#divInvoice").hide();
      $("#btnInvoicePrint").hide();
  
      if (this.radioSelected == "barcode") {
        $("#optionCheckBox").show();
        $("#divBarcode").show();
        $("#btnBarcodePrint").show();
        
      }
      if (this.radioSelected == "invoice") {
        $("#optionCheckBox").hide();
        $("#divInvoice").show();
        $("#btnInvoicePrint").show();
      }    
  
      $("#trackingStep1").attr("style", this.trackingStepBackgroundColor.step1)
      $("#trackingStep2").attr("style", this.trackingStepBackgroundColor.step2)
      $("#trackingStep3").attr("style", this.trackingStepBackgroundColor.step3)
      $("#trackingStep4").attr("style", this.trackingStepBackgroundColor.step4)
      $("#trackingStep5").attr("style", this.trackingStepBackgroundColor.step5)
    },
  };
  </script>